export default {
	data() {
		return {
			persent: '60',
			n: true,
			number: 1,
			id: '',
			question: '',
			details: '',
			introduce: '',
			poolDetail: '',
			month: false,
			year: false,
			num: '6',
			numlist: [6, 12, 24, 36],
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '存储中心介绍'
				},
				{
					state: 1,
					text: '存力介绍'
				},
				{
					state: 2,
					text: '常见问题'
				}
			],
			yearList: ['三年'],
			yearIndex: 0,
			laber: ''
		};
	},
	onLoad(option) {
		this.poolDetail = JSON.parse(option.data);
		this.id = this.poolDetail.id;
		this.laber = this.poolDetail.laber
		this.persent = parseInt((this.poolDetail.hashrate / this.poolDetail.info) * 100);
		console.log(this.poolDetail)
		console.log(this.id)
		//存储中心介绍
		uni.request({
			url: this.url + 'cloudintroduces/',
			method: 'GET',
			data: {
				cloudid: this.id
			},
			success: res => {
				console.log(res)
				this.introduce = res.data.message.introduce;
				this.details = res.data.message.details;
				this.question = res.data.message.question;
			}
		});
		//常见问题
		/* uni.request({
			url: this.url + 'cloudask/',
			method: 'GET',
			data: {
				cloudid: this.id
			},
			success: res => {
				console.log(res)
				this.question = res.data.data;
			}
		}); */
	},
	methods: {
		toggleYear(index) {
			this.yearIndex = index;
		},
		next: function() {
			this.n = false;
		},
		back: function() {
			this.n = true;
		},
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		tabClick(index) {
			var that = this;
			that.tabCurrentIndex = index;
		},
		buynow: function() {
			uni.showToast({
				title: '预购即将开启',
				icon: 'none',
				duration: 3000
			});
		},
		buy_month: function() {
			var that = this;
			that.month = true;
			that.year = false;
		},
		buy_year: function() {
			this.year = true;
			this.month = false;
		},
		
		num_reduce() {
			if (this.num == 6) {
				uni.showToast({
					icon: 'none',
					title: '不能更小了'
				})
			} else if (this.num == 12) {
				this.num = 6
			} else if (this.num == 24) {
				this.num = 12
			} else if (this.num == 36) {
				this.num = 24
			}

		},
		
		add_num() {
			if (this.num == 36) {
				uni.showToast({
					icon: 'none',
					title: '不能更大了'
				})
			} else if (this.num == 24) {
				this.num = 36
			} else if (this.num == 12) {
				this.num = 24
			} else if (this.num == 6) {
				this.num = 12
			}

		},
		getNum:function(e){
			this.number=e.detail.value;
		},
		buynum_add() {
			this.number++;
			console.log(this.number)
		},
		buynum_reduce() {
			if (this.number <= 1) {
				uni.showToast({
					icon: 'none',
					title: '不能更小了'
				})
			} else {
				this.number--;
			}
			console.log(this.number)
		}
	}
};
